<template>
  <div>
    <template v-for="(categorie, index) in gridCategories">
      <div
        :id="'category' + index"
        class="content_scrore content__ mt-5"
        :key="categorie.id"
      >
        <div
          @click="
            () => {
              activeCategory = index;
              scrollToRef();
            }
          "
          :ref="'catRef' + index"
          class="panel_header content"
        >
          <div class="title">{{ categorie?.name }}</div>
          <div class="note">0 / 0</div>
        </div>
        <template v-if="activeCategory == index">
          <div class="content___">
            <template v-for="item in categorie.items">
              <div class="panel_body" :key="item.id">
                <div class="content_status">
                  <p>{{ item.name }} : {{ item.description }}</p>
                  <div class="flex">
                    <div class="status">
                      <template v-for="note in item.notes">
                        <div class="flexbox" :key="note.id">
                          <div class="flex">
                            {{ note.name }}
                            <div
                              class="tooltip ml-2"
                              @click="handleShowNote(note)"
                            >
                              <i
                                v-if="
                                  selectedNote != null &&
                                  selectedNote?.id == note.id
                                "
                                class="icon-info-bold"
                              ></i>
                              <i v-else class="icon-info-light"></i>
                            </div>
                          </div>
                          <div class="main-account flex">
                            <template
                              v-for="(
                                member, index
                              ) in calibration.member_calibration"
                            >
                              <template
                                v-if="
                                  checkscorigvoted(
                                    note.id,
                                    item,
                                    member.id,
                                    item.record_id,
                                  )
                                "
                              >
                                <Account
                                  :firstName="member.first_name"
                                  :lastName="member.last_name"
                                  :bgColor="member.avatar_color"
                                  :key="index + '_avatar'"
                                />
                              </template>
                            </template>
                          </div>
                        </div>
                      </template>

                      <div
                      
                        v-for="(
                          member, index
                        ) in calibration.member_calibration"
                        :key="index"
                      >
                        <template
                          v-if="checkCommentairevoted(item.id, member.id)"
                        >
                          <div
                           class="details_account mt-5"
                            :key="index + '_details_account'"
                          >
                            <div class="flexbox">
                              <Account
                                :firstName="member.first_name"
                                :lastName="member.last_name"
                                :bgColor="member.avatar_color"
                                :key="index + '_avatar'"
                              />
                              <div
                                class="text_"
                                :style="
                                  'color:' +
                                  member.avatar_color +
                                  ';' +
                                  'background:' +
                                  member.avatar_color +
                                  '10' +
                                  ';' +
                                  'border:1px solid' +
                                  member.avatar_color
                                "
                                :key="'comments_members_' + index"
                              >
                                {{
                                  getCommentaireMemberVoted(item.id, member.id)
                                }}
                              </div>
                            </div>
                          </div>
                        </template>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </template>
          </div>
          <div class="panel_footer" :key="categorie.id + '_btn'">
            <button
              @click="
                () => {
                  activeCategory--;
                  scrollToRef();
                }
              "
              v-if="
                gridCategories.length > 1 &&
                ((activeCategory > 0 &&
                  activeCategory < gridCategories.length - 1) ||
                  activeCategory == gridCategories.length - 1)
              "
              class="prev mr-3"
              id="prev2"
            >
              Précédent
            </button>
            <button
              @click="
                () => {
                  activeCategory++;
                  scrollToRef();
                }
              "
              v-if="
                gridCategories.length > 1 &&
                activeCategory < gridCategories.length - 1
              "
              class="next"
              id="next1"
            >
              Continuer
            </button>
          </div>
        </template>
      </div>
    </template>
    <DrawerInfo
      :isOpened="showDrawer"
      @close="
        () => {
          showDrawer = false;
          setSelectedNote(null);
        }
      "
    ></DrawerInfo>
  </div>
</template>

<script>
import $ from 'jquery';
import Account from '@/components/FormWidgets/AvatarColorsRandom/AvatarColor.vue';
import { mapActions, mapGetters } from 'vuex';
import DrawerInfo from '@/components/DrawerForm/Records/DrawerInfo.vue';
export default {
  props: ['calibration', 'activeRecord'],
  components: {
    Account,
    DrawerInfo,
  },
  data() {
    return {
      activeSection: 1,
      activeCategory: 0,
      gridCategories: [],
      showDrawer: false,
    };
  },
  methods: {
    ...mapActions('record', ['setSelectedNote']),
    handleShowNote(note) {
      this.setSelectedNote(note);
      this.toggleDrawer();
    },
    toggleDrawer() {
      this.showDrawer = !this.showDrawer;
    },
    toggleAccordion(section) {
      this.activeSection = this.activeSection === section ? null : section;
    },
    scrollToRef() {
      // Access the referenced element using $refs
      const targetElement = this.$refs['catRef' + this.activeCategory][0];
      const ti = document.getElementById('content_page');

      // Check if the element exists
      if (targetElement) {
        // Scroll to the referenced element
        targetElement.scrollIntoView({
          behavior: 'smooth',
          block: 'start',
          inline: 'nearest',
          offsetTop: targetElement.scrollTop + 50,
        });
      }
      /*  targetElement.scrollTo({
          top:  300,
          behavior: 'smooth'
        }); */
    },
    checkscorigvoted(id, item, member, recordId) {
      if (this.calibration.evaluation_user != undefined) {
        let evals = this.calibration.evaluation_user;
        const result = evals.find(
          (item) =>
            item.note_id === id &&
            item.member_calibration_id === member &&
            item.record_id == this.activeRecord.id,
        );
        if (result != undefined) {
          item.commentaire = result.commentaire;
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },

    checkCommentairevoted(id, member) {
      if (this.calibration.evaluation_user != undefined) {
        let evals = this.calibration.evaluation_user;
        const result = evals.find(
          (item) =>
            item.item_id === id &&
            item.member_calibration_id === member &&
            item.commentaire != null &&
            item.record_id == this.activeRecord.id
        );
        if (result != undefined) {
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },

    getCommentaireMemberVoted(id, member) {
      if (this.calibration.evaluation_user != undefined) {
        let evals = this.calibration.evaluation_user;
        const result = evals.find(
          (item) =>
            item.item_id === id && item.member_calibration_id === member,
        );
        if (result != undefined) {
          return result.commentaire;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },
  },
  watch: {
    activeRecord(record) {
      this.gridCategories = record ? record.grid?.categories : [];
    },
  },
  mounted() {
    $(document).ready(function () {
      $('.panel_header').click(function () {
        var target = $(this).data('toggle');

        // Remove the "active" class from all buttons
        $('.panel_header').removeClass('active');

        // Remove the "active" class from all toggle elements
        $('.panel_body').removeClass('active');

        // Close all other toggle elements
        $('.panel_body')
          .not('#' + target)
          .slideUp();

        // Toggle the target element
        $('#' + target).slideToggle();

        // Add the "active" class to the clicked button
        $(this).addClass('active');

        // Add the "active" class to the target element
        $('#' + target).toggleClass('active');
      });
    });
  },
  computed: {
    ...mapGetters('record', ['getSelectedNote']),
    selectedNote() {
      return this.getSelectedNote;
    },
  },
};
</script>
