<template>
  <transition name="slide">
  <div v-if="show" :class="{ drawer: true, show: show }" class="show">
    <div class="header_drawer">
      <div class="title">
        <h4>{{ $t("views.agents.massImport") }}</h4>
      </div>
      <div
        class="content-button btn-add"
        style="justify-content: flex-end"
        v-if="(uploading === false && uploadingSuccess === false) || importError"
      >
        <button class="close mr-5" @click="close()">
          {{ $globals.capitalizeFirstLetter($t("common.cancel")) }}
          <i class="icon-exit"></i>
        </button>
        <Button @when-cliked="uploadFile()" :text="$t('views.agents.import')" />
      </div>
      <div
        v-if="uploading && file !== null && importError === false"
        class="content-button btn-add"
        style="justify-content: flex-end"
      >
        <button class="close mr-5" style="color: #f5ff63; text-decoration: none;">
          {{ $t("views.agents.VeuillezPatienter") }}
        </button>
      </div>
      <div
        v-if="uploading === false && uploadingSuccess === true && importError === false"
        class="content-button btn-add"
        style="justify-content: flex-end"
      >
        <button class="close mr-5" @click="newUpload()" style="color: #f5ff63; text-wrap: nowrap;">
          {{ $t("views.agents.NouvelImport") }}
        </button>
        <Button @when-cliked="close()" :text="$t('views.agents.ArchivageTerminer')" />
      </div>
    </div>

    <div class="body_drawer importFile">
      <form id="import-masse">
      <div
        class="form-group-flex"
        v-show="(uploading === false && uploadingSuccess === false) || importError"
      >
      <div class="form-field">
        <label for="">{{ $t("views.agents.labelFichierCSV") }}</label>
        <!-- <div class="custom-file-input">
            <input
                type="file"
                accept=".csv"
                class="import"
                id="file"
                @change="handleFileUpload"
            />
            <label for="file" class="file-label">Parcourir</label>
        </div> -->

        <div class="custom-file-upload">
          <input type="file" id="file" ref="fileInput" @change="handleFileUpload" />
          <div class="file-upload-buttons" @click.prevent="selectFile">
            <span class="file-name">{{ fileNameInput }}</span>
            <button>Parcourir</button>
          </div>
        </div>

                                        
        <p v-if="importError === false" class="pt-3 mb-7">
            {{ $t("views.agents.dessinCSV") }}
            <a :href="csvFileLik">{{ $t("views.agents.lienTelechargerGabarit") }}</a><br>
            <a class="notice-button" @click="showDrawerNotice()">{{ $t('views.agents.lienNotice') }}</a>
        </p>
        <div class="error mb-4 pt-2" v-if="importError">
            {{ importErrorMessage }}
        </div>
    </div>
      </div>
      <div v-if="importError === false">
        <div
          v-if="uploading && file !== null && importError === false"
          class="patienter py-7 text-center"
        >
          <p class="mb-3">
            {{ $t("views.agents.loadingMsgP1") }} <br>{{ $t("views.agents.loadingMsgP2") }}
          </p>
          <LoaderImport />
        </div>
     
        <div class="recapitalitf" v-if="uploading === false && uploadingSuccess === true">
     
          <div class="importation-finaliser mb-5">
            <div class="py-4">
              <h3 class="pb-3">
                <i class="icon-checked_green pr-3"></i>
                <strong>{{ $t("views.agents.finalImportation") }}</strong>
              </h3>
              <p>{{ $t("views.agents.recapImportation") }}</p>
              <p>{{ $t("views.agents.recapline2") }}</p>
            </div>
          </div>
          <ul class="mb-5">
            <li>{{ successfulRows }} succès</li>
            <li>
              {{ failedRows.length === 0  ? failedRows + ' erreur' : failedRows + ' erreurs' }} 
              <a href="#" @click.prevent="downloadFailedRowsCSV">Télécharger</a>
            </li>
          </ul>

          <TableComponent :columns="columns" :data="data" class="mt-2" />
        </div>
      </div>
      </form>
      <Notice :isOpened="showNotice" @close="showNotice = false" />
    </div>
    <div id="overlay-" @click="close()"></div>
  </div>
</transition>
</template>

<script>
//import InputField from '@/components/FormWidgets/InputField.vue';
import Button from "@/components/FormWidgets/Button.vue";
import TableComponent from "@/components/Table/Table.vue";
import { axios } from "../../../store/services/axios.js";
import Loader from "@/components/AnimationScribb/Loader.vue";
import LoaderImport  from "@/components/AnimationScribb/LoaderBtn.vue";
import Notice from "@/components/DrawerForm/Agent/Notice.vue";
import { mapActions, mapGetters } from "vuex";
export default {
  components: {
    TableComponent,
    Button,
    LoaderImport,
    Notice,
  },
  data() {
    return {
      csvFileLik:
        process.env.VUE_APP_SERVER_URL + "/fichier-scribb/scb_import_agents.csv",
      showImportMasse: false,
      file: null,
      uploading: false,
      uploadingSuccess: false,
      data: [],
      dataFailedRows: [],
      columns: [
        {
          key: "fullName",
          label: "Agent",
          render: (text, record) => {
            return `<span>${record.firstName} ${record.lastName} <br/> <p>${record.id}</p></span>`;
          },
        },
        {
          key: "callCenterName",
          label: "Call-center",
        },
        {
          key: "roleName",
          label: "Rôle",
        },
        {
          key: "message",
          label: "Statut",
          render: (text, record) => {
            const parsedobj = JSON.parse(JSON.stringify(record));
            if (parsedobj.errors) {
              let html = "<span>";
              Object.keys(parsedobj.errors).forEach((key) => {
                if (Array.isArray(parsedobj.errors[key])) {
                  parsedobj.errors[key].forEach((error) => {
                    const replacedError = this.$t(`errors.${error}`).replace(
                      / - /g,
                      "<br/>"
                    );
                    html += `${replacedError}</br>`;
                  });
                } else {
                  const replacedError = this.$t(
                    `errors.${parsedobj.errors[key]}`
                  ).replace(/ - /g, "<br/>");
                  html += `${replacedError}</br>`;
                }
              });
              html += "</span>";
              return html;
            } else {
              return `<span>${this.$t(`success.${parsedobj.status}`)}</span>`;
            }
          },
        },
      ],
      importError: false,
      importErrorMessage: "",
      failedRows: 0,
      successfulRows: 0,
      showNotice: false,
      fileName: "",
      fileNameInput: 'Aucun fichier choisi',
    };
  },
  props: ["show"],
  computed: {
    ...mapGetters("agent", ["getPagination"]),
  },
  methods: {
    ...mapActions("agent", ["getData", "setPagination"]),
    close() {
      this.$emit("close");
      this.newUpload();
    },
    newUpload() {
      this.showImportMasse = false;
      this.importError = false;
      this.importErrorMessage = "";
      this.resetFileUpload();
      const form = document.getElementById('import-masse');
      form.reset();
      this.fileNameInput = 'Aucun fichier choisi';
    },
    selectFile() {
      this.$refs.fileInput.click();
    },
    showDrawerNotice(){
      this.showNotice = !this.showNotice;
    },
    async handleFileUpload(event) {
      const file = event.target.files[0];
      if (file) {
        this.file = file;
        this.fileNameInput = file.name;
      }
      /* try {
        // this.file = null;
        event.preventDefault();
        this.importError = false;
        this.importErrorMessage = "";
        this.uploadingSuccess = false;

        const file = event.target.files[0];
        if (file) {
          this.file = file;
        }
      } catch (err) {
        if (err.response && err.response.data && err.response.data.message) {
          this.importError = true;
          this.importErrorMessage = err.response.data.message;
        } else {
          this.importError = true;
          this.importErrorMessage =
            "Une erreur s'est produite lors du téléchargement du fichier.";
        }
        // this.resetFileUpload();
      } */
    },

    async uploadFile() {
      if (this.file) {
        try {
          const fileName = this.file.name;
          this.importError = false;
          this.importErrorMessage = "";
          this.uploadingSuccess = false;
          this.uploading = true;
          const formData = new FormData();
          formData.append("file", this.file);

          const config = {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("auth")}`,
            },
          };

          const response = await axios.post("/agent/upload", formData, config);
          if (response && response.data.data) {
            const { data } = response.data.data;
            this.uploading = false;
            this.uploadingSuccess = true;
            this.data = [...data.failed_rows];
            this.failedRows = data.failed_rows.length;
            this.successfulRows = data.successful_rows.length;

            

            this.dataFailedRows = data.failed_rows;

            if (this.failedRows > 0) {

        
             // this.importError = true;
              this.importErrorMessage = this.$t(`errors.${response.data.message}`);
             //this.resetFileUpload();
            }
            if (this.successfulRows > 0) {
              this.setPagination({
                ...this.getPagination,
                ["current_page"]: 1,
              });
              this.$store.dispatch("agent/getData");
            }
          }
        } catch (err) {
          if (err.response && err.response.data && err.response.data.message) {
            this.importError = true;
            this.importErrorMessage = this.$t(`errors.${err.response.data.message}`);
          } else {
            this.importError = true;
            this.importErrorMessage =
              "Une erreur s'est produite lors du téléchargement du fichier.";
          }

          // this.resetFileUpload();
        }
      }
    },
    resetFileUpload() {
      this.file = null;
      document.getElementById("file").value = "";
      this.uploadingSuccess = false;
      this.uploading = false;

      this.fileNameInput = 'Aucun fichier choisi';
      const form = document.getElementById('import-masse');
      form.reset();
    },
    downloadFailedRowsCSV() {
      if (this.dataFailedRows.length > 0) {
        const filteredData = this.dataFailedRows.map((row) => {
          const filteredRow = { ...row };

          if (filteredRow.hasOwnProperty("hermesId")) {
            filteredRow["identifiant"] = filteredRow["hermesId"];
            delete filteredRow["hermesId"];
          }

          if (filteredRow.hasOwnProperty("firstName")) {
            filteredRow["first_name"] = filteredRow["firstName"];
            delete filteredRow["firstName"];
          }

          if (filteredRow.hasOwnProperty("lastName")) {
            filteredRow["last_name"] = filteredRow["lastName"];
            delete filteredRow["lastName"];
          }

          if (filteredRow.hasOwnProperty("callCenterId")) {
            filteredRow["call_center_id"] = filteredRow["callCenterId"];
            delete filteredRow["callCenterId"];
          }

          if (filteredRow.hasOwnProperty("roleId")) {
            filteredRow["role_id"] = filteredRow["roleId"];
            delete filteredRow["roleId"];
          }

          delete filteredRow.roleName;
          delete filteredRow.callCenterName;
          delete filteredRow.errors;
          delete filteredRow.id;

          return filteredRow;
        });

        const csvContent = this.convertToCSV(filteredData);
        const blob = new Blob([csvContent], { type: "text/csv" });
        const blobURL = window.URL.createObjectURL(blob);
        const a = document.createElement("a");
        a.href = blobURL;
        a.download = "failed_rows.csv";
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        window.URL.revokeObjectURL(blobURL);
      }
    },
    convertToCSV(data) {
      const header = Object.keys(data[0]).join(",");
      const rows = data.map((row) => Object.values(row).join(","));
      return `${header}\n${rows.join("\n")}`;
    },
  },
};
</script>
