<template>
    <div class="form-field search-field autocomplete" :class="{ fieldError: hasError }">
        <label class="control-label" :for="inputId">{{ label }}</label>
        <template>
            <div class="search">
                <i class="icon-search"></i>
            </div>
            <input v-model="searchTerm" @input="filterItems" :type="type" :placeholder="placeholder" />
        </template>
        <div class="error" v-if="hasError">{{ errorMessage }}</div>
        <div class="list-search">
            <ul v-show="this.searchTerm.length > 2 && collection.length > 0 && !isFetchingData">
                <li v-for="(item, index) in collection" @click="selectItem(item)" :key="index">
                    {{ item[keyLabel] }}
                </li>
            </ul>
            <ul v-show="isFetchingData">
                <li>Loading...</li>
            </ul>
            <ul v-show="this.searchTerm.length > 2 && collection.length == 0 && !isFetchingData">
                <li>{{ noResultsMessage }}</li>
            </ul>
        </div>
    </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
export default {
    data() {
        return {
            searchTerm: '',
            // filteredItems: [],
            // showResults: false,
            noResultsMessage: 'Aucun résultat trouvé.',
        };
    },
    props: {
        type: {
            type: String,
            default: 'text', // Input type (e.g., text, password, email, etc.)
        },
        storeModuleName: String,
        placeholder: String,
        inputId: String, // Input ID
        label: String, // Label text
        items: Array,
        itemsSelected: Array,
        keyValue: String,
        keyLabel: String,
        keyLabelhermesId: String,
        keyArray: String,
        isAgent: Boolean,
        hasError: {
            type: Boolean,
        },
        errorMessage: {
            type: String,
        },
    },
    computed: {
        collection() {
            return this.$store.getters[`${this.storeModuleName}/getCollection`];
        },
        filter() {
            return this.$store.getters[`${this.storeModuleName}/getFilter`];
        },
        isFetchingData() {
            return this.$store.getters[`${this.storeModuleName}/getIsFetchingRecords`];
        },
    },
    methods: {
        filterItems() {
            this.searchTerm = this.searchTerm.trim();

            if (this.searchTerm.length > 2) {
                this.$store.dispatch(`${this.storeModuleName}/setFilter`, {
                    ...this.$store.getters[`${this.storeModuleName}/getFilter`],
                    ["keyword"]: this.searchTerm,
                });

                // if (this.collection.length > 0) {
                //     this.showResults = true;
                // } else {
                //     this.showResults = false;
                // }
            } else {
                // this.showResults = false;
            }
        },
        selectItem(item) {
            const selectedValue = item[this.keyLabel];
            this.searchTerm = selectedValue;
            // this.showResults = false;
            // Emit the custom event to the parent
            this.$emit('item-selected', item, this.keyArray);
            this.searchTerm = '';
        },
        removeItem(item) {
            this.$emit('remove-item', item[this.keyValue], this.keyArray);
        },
    },
    watch: {
        'filter.keyword': function () {
            this.$store.dispatch(`${this.storeModuleName}/setCollection`, []);
            this.$store.dispatch(`${this.storeModuleName}/getData`);
        },
    },
    mounted() {
        // this.$store.dispatch(`${this.storeModuleName}/setCollection`, []);
        // this.$store.dispatch(`${this.storeModuleName}/setFilter`, {
        //         ...this.$store.getters[`${this.storeModuleName}/getFilter`],
        //         ["withPagination"]: 0,
        //     });
        // this.$store.dispatch(`${this.storeModuleName}/getData`);
    },
};
</script>