<template>
    <div>
      <div class="table scroll-x">
        <div class="table-row header" :class="data.length > 0 ? '' : 'empty'">
          <div
            class="table-cell"
            v-if="multipleSelect && multipleSelect === true"
          >
            <div class="form-check" v-if="!selectAgents">
              <input
                v-model="isAllSelected"
                @change="handleSelectRows"
                type="checkbox"
                id="all-list"
              />
              <label for="all-list"></label>
            </div>
          </div>
          <div class="table-cell" v-if="hasActions && hasActions === true"></div>
          <div class="table-cell" v-for="column in columns" :key="column.key">
            <ColumnRender
              :column="column"
              :sortOrder="sortOrder"
              :sortKey="sortKey"
              @onSortTriggered="(key) => handleSortKeyChanged(key)"
            />
          </div>
        </div>
        <template v-if="!isLoading">
          <template v-if="data.length > 0">
            <div class="tbody">
            <div
              class="table-row"
              :class="selectAgents ? getRowClasses(item) : ''"
              v-for="(item, index) in data"
              :key="selectAgents ? 'agent' + index : index"
              :style="{ cursor: redirection ? 'pointer' : '' }"
            >
              <div
                class="table-cell"
                v-if="multipleSelect && multipleSelect === true"
              >
                <div class="form-check">
                  <input
                    :disabled="getRowClasses(item) === 'disable-line'"
                    @change="handleSelectedRow(item)"
                    type="checkbox"
                    :id="
                      selectAgents
                        ? 'item-list-agent' + index
                        : 'item-list' + index
                    "
                    :value="item[keySelection]"
                    v-model="selectedKeys"
                  />
                  <label
                    :for="
                      selectAgents
                        ? 'item-list-agent' + index
                        : 'item-list' + index
                    "
                  ></label>
                </div>
              </div>
              <div class="table-cell" v-if="hasActions && hasActions === true">
                <div  class="form-document" v-if="item.status && item.status === 'finished'" @click="handleeClickedRow(item)">
                  <i class="icon-document"></i>
                </div>
               
                <Actions v-else-if="item.status && item.status !== 'cancel'"
                  :actions="actions"
                  :index="index"
                  :onUpdate="
                    () => {
                      onUpdateItem(index, item);
                    }
                  "
                  :onArchive="
                    () => {
                      onArchiveItem(index, item);
                    }
                  "
                  :onDuplicate="
                    () => {
                      onDuplicateItem(index, item);
                    }
                  "
                  :onUnArchive="
                    () => {
                      onUnArchiveItem(index, item);
                    }
                  "
                  :onCancel="
                    () => {
                      onCancelItem(index, item);
                    }
                  "
                  :onGetVersion="
                    () => {
                      onGetItemVersion(index, item);
                    }
                  "
                />
              </div>
              <div class="table-cell" v-for="column in columns" :key="column.key">
                <TableCellRender
                  :column="column"
                  :data="item"
                  @row-clicked="
                    (item) => {
                      handleRowClicked(item);
                    }
                  "
                />
              </div>
            </div>
            </div>
          </template>
  
          <div v-else class="table-row empty-data text-center">
            <div class="table-empty">
              <h6>
                {{ msgEmptyTable }}
              </h6>
              <button v-if="withButton" @click="handleButton()">
                {{ textButton }}
              </button>
            </div>
          </div>
        </template>
  
        <template v-else>
          <div class="table-row empty-data text-center">
            <div class="table-empty">
              <Loader />
            </div>
          </div>
        </template>
      </div>
    </div>
  </template>
  <script>
  import TableCellRender from '../Table/TableCellRender.vue';
  import ColumnRender from '../Table/ColumnRender.vue';
  import Actions from '../Table/Actions.vue';
  import Loader from '../AnimationScribb/Loader.vue';
  import { mapActions, mapGetters } from 'vuex';
  export default {
    props: {
      columns: Array,
      data: Array,
      multipleSelect: Boolean,
      onSelectItems: {
        type: Function,
        required: false,
        default: function () {},
      },
      onSelectItem: {
        type: Function,
        required: false,
        default: function () {},
      },
      hasActions: Boolean,
      actions: Object,
      onDeleteItem: {
        type: Function,
        required: false,
        default: function () {},
      },
      onUpdateItem: {
        type: Function,
        required: false,
        default: function () {},
      },
      onArchiveItem: {
        type: Function,
        required: false,
        default: function () {},
      },
      onUnArchiveItem: {
        type: Function,
        required: false,
        default: function () {},
      },
      onDuplicateItem: {
        type: Function,
        required: false,
        default: function () {},
      },
      onCancelItem: {
        type: Function,
        required: false,
        default: function () {},
      },
      isLoading: {
        type: Boolean,
        default: false,
      },
      onClickRow: {
        type: Function,
        default: () => {},
      },
      redirection: {
        type: Boolean,
        default: false,
      },
      selectAgents: {
        type: Boolean,
        default: false,
      },
      keySelection: {
        type: String,
        required: false,
        default: '',
      },
      msgEmptyTable: {
        type: String,
        default: function () {
          return this.$globals.capitalizeFirstLetter(this.$t('common.norecords'));
        },
      },
      withButton: {
        type: Boolean,
        default: false,
      },
      textButton: {
        type: String,
        default: '',
      },
      onGetItemVersion: {
        type: Function,
        required: false,
        default: function () {},
      },
      agentDashboard:{
        type: Boolean,
        required: false,
        default: function () {},
      },
      enabledScrollCss:{
        type: Boolean,
        required: false,
      }, 
      teamDashboard:{
        type: Boolean,
        required: false,
        default: function () {},
      },
     },
    data() {
      return {
        selectedRows: [],
        roleSelected: null,
        isAllSelected: false,
        selectedKeys: [],
        sortKey: null,
        sortOrder: 'asc',
        enabledCss: false
      };
    },
    mounted() {},
    components: {
      TableCellRender,
      ColumnRender,
      Loader,
      Actions,
    },
    methods: {
      ...mapActions("agent", ["getTopFlopAgentsData"]),
      ...mapActions('team', ['getTopFlopTeamData', 'setTopFlop']),
      ...mapActions('calibrage', ['setModel']),
  
      handleRowClicked(item) {
        if (this.redirection && item.status !== 'cancel') this.$emit('row-clicked', item);
      },
      handleSelectRows() {
        if (this.isAllSelected) {
          this.selectedRows = this.data;
        } else {
          this.selectedRows = [];
        }
        this.$emit('onSelection', this.selectedRows);
      },
      handleeClickedRow(item) {
        this.setModel({
          id: item.id,
          recordCalibrations: item.recordCalibrations.map((item, index) => ({
            index: index,
            id: item.recordId.id,
            name: item.recordId.name,
            firstName: item.recordId.agentId.firstName,
            lastName: item.recordId.agentId.lastName,
            callCenter: item.recordId.agentId.callCenterId.name,
          })),
        });
        this.$router.push({ name: "compterendu", params: { id: item.id } });
      },
      handleSelectedRow(item) {
        if (
          this.selectedRows.find(
            (x) => x[this.keySelection] == item[this.keySelection],
          )
        ) {
          this.selectedRows = this.selectedRows.filter(
            (x) => x[this.keySelection] != item[this.keySelection],
          );
          if (this.selectedRows.length == 0 && this.selectAgents)
            this.roleSelected = null;
        } else {
          this.selectedRows.push(item);
          if (this.selectAgents) this.roleSelected = item.roleId;
        }
        this.$emit('onSelection', this.selectedRows);
      },
      handleSortKeyChanged(key) {
        this.sortKey = key;
        if (this.sortOrder === 'asc') {
          this.sortOrder = 'desc';
        } else {
          this.sortOrder = 'asc';
        }
        this.$emit('onSort', {
          sortKey: this.sortKey,
          sortOrder: this.sortOrder,
        });
      },
      setSelectedRows(data) {
        this.selectedRows = data;
        this.selectedKeys = [];
      },
      handleButton() {
        this.$emit('onButtonClicked');
      },
      getRowClasses(item) {
        if (this.roleSelected && this.roleSelected != item.roleId)
          return 'disable-line';
      },
  
   
    },
    computed: {
      ...mapGetters({ campaign: "campaign/getCampagneInfo" }),
      ...mapGetters("agent", ["getTopFlopCollection","getPagination"]),
      agentCollection(){
        return this.getTopFlopCollection;
      }
  
    },
    watch: {
      selectedRows(newRows) {
        this.selectedKeys = newRows.map((row) => row[this.keySelection]);
        if (newRows.length == 0) {
          this.isAllSelected = false;
          return;
        }
        if (newRows.length == this.data.length) {
          this.isAllSelected = true;
          return;
        }
        if (newRows.length != this.data.length) {
          this.isAllSelected = false;
          return;
        }
      },
    },
  };
  </script>
  