<template>
  <div class="form-field">
    <label class="control-label">
      {{ $globals.capitalizeFirstLetter($t('components.grid.enable')) }}
    </label>

    <div class="switch-button">
      <input
        v-model="checkboxValue"
        class="switch-button-checkbox"
        type="checkbox"
        @change="handleValueChange"
      />
      <label class="switch-button-label" for="">
        <span class="switch-button-label-span">Oui</span>
      </label>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    defaultValue: {
      type: Number,
      default: 1,
    },
  },
  data() {
    return { checkboxValue: !!this.defaultValue };
  },
  methods: {
    handleValueChange(event) {
      this.checkboxValue = event.target.checked;
      this.$emit('value-changed', this.checkboxValue ? 1 : 0);
    },
  },
  watch: {
    defaultValue(newValue) {
      this.checkboxValue = !!newValue;
    },
  },
};
</script>
