<template>
  <div>


    <div class="form-field mb-7" >
      <label id="select-label" v-if="label">
        {{ label }}
      </label>
      <div ref="items" class="custom-select" :class="{ fieldError: hasError }" @blur="open = false">
        <div class="selected" :class="{ open: open }" @click="openSelected" :id="'custom-select' + index">
          <i class="icon-down"></i>
          <div style="display: flex; align-items: center">
            <template v-if="selected && selected.leftContent != null">
              <div v-html="selected?.leftContent"></div>
            </template>
            <span>
              {{ selected?.text }}
            </span>
          </div>
        </div>
        <div class="items" :class="{ selectHide: !open }" :id="'items_' + index">
          <div ref="search" class="select-search">
            <input type="text" v-model="keySearch" @input="handleKeySearch" />
          </div>
          <div class="space" style="margin-top: 55px">
            <template v-if="filteredOptions.length > 0">
              <template v-for="option of formatedOptions">
                <div :key="option.id" :class="{ 'item': true, 'selected-single': defaultSelected === option.id }"
                  v-if="option.text.toLowerCase().includes(keySearch.toLowerCase()) && !option.hidden" @click="
        selected = option;
      open = false;
      onSelect(option);
      " class="item" style="display: flex; align-items: center">
                  <template v-if="option.leftContent != null">
                    <div v-html="option.leftContent"></div>
                  </template>
                  <span>{{ option.text }}</span>
                </div>
              </template>
            </template>
            <template v-else>
              <div class="no-results">Aucun résultat</div>
            </template>
          </div>
        </div>
      </div>
      <div class="error mb-4 pt-2" v-if="hasError">{{ errorMessage }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    options: {
      type: Array,
      required: true,
    },
    defaultSelected: {
      type: [String, Number, Object],
      required: false,
      default: null,
    },
    hasError: {
      type: Boolean,
    },
    errorMessage: {
      type: String,
    },
    onSelect: {
      type: Function,
      default: () => { },
    },
    label: {
      type: String,
      required: false,
    },
    keyValue: String,
    keyLabel: String,
    isDisabled: Boolean,
    id: Number,
  },
  data() {
    return {
      selected: null,
      open: false,
      formatedOptions: [],
      keySearch: '',
      index: null
    };
  },
  created() {
    this.formatOptions();
    this.generateRandomString()
  },
  mounted() {
  
    this.setWidth();
    window.addEventListener('resize', this.setWidth); // Recalculate width on window resize
  },
  destroyed() {
    window.removeEventListener('resize', this.setWidth); // Remove event listener on component destroy
  },
  methods: {
    handleKeySearch(event) {
      const inputValue = this.keySearch.trim();
      this.$emit('lengthInputValue', inputValue);
    },
    generateRandomString() {
      const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
      let result = '';
      const charactersLength = characters.length;
      for (let i = 0; i < 10; i++) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
      }
      this.index =  result;
    },
    formatOptions() {
      this.formatedOptions = [];
      for (let index in this.options) {
        let formatedItem = { ...this.options[index] };
        if (!formatedItem.id) {
          formatedItem.id = formatedItem[this.keyValue];
        }
        if (!formatedItem.text) {
          formatedItem.text = formatedItem[this.keyLabel];
        }
        this.formatedOptions.push(formatedItem);
        if (this.defaultSelected && formatedItem.id == this.defaultSelected) {
          this.selected = formatedItem;
        }
      }
    },
    openSelected() {
      if (!this.isDisabled) this.open = !this.open;
    },
    setWidth() {
      const itemsWidth = this.$refs.items.offsetWidth;
      this.$refs.search.style.width =
        Number(itemsWidth) -
        8 /* - (this.options.length > 4 ? 6 : 3)  */ +
        'px'; // Apply the width to the .search div
    },
    setSelected(value) {
      const id = this.formatedOptions.find((x) => x.id == value)
        ? this.formatedOptions.find((x) => x.id == value).id
        : undefined;
      const text = this.formatedOptions.find((x) => x.id == value)
        ? this.formatedOptions.find((x) => x.id == value).text
        : undefined;
      if (id != undefined && text != undefined) {
        this.selected = {
          id: id,
          text: text,
        };
      } else {
        this.selected = value;
      }
    },

    Close() {
      const togleClose = () => {
        this.open = false
      }
      const btn = document.getElementById("custom-select" + this.index);
      const items = document.getElementById("items_" + this.index);
      // Hide the dropdown menu if the user clicks anywhere else on the page
      document.addEventListener("click", function (event) {
        if (!btn.contains(event.target) && (!items.contains(event.target))) {
          togleClose();
        }
      });
    }
  },
  computed: {
    filteredOptions() {
      return this.formatedOptions.filter((option) =>
        option.text.toLowerCase().includes(this.keySearch.toLowerCase()),
      );
    },
  },
  watch: {
    options() {
      this.formatOptions();
    },
    selected() {

    },
  },
  mounted() {
    this.Close();

  },
};
</script>
